import { defineComponent } from "vue";
import EditorBlock from "@/components/EditorBlock/index.vue";
import Drawflow from "drawflow";
import * as Vue from "vue";
import EventEmitter from "@/util/EventEmitter";
import useVuelidate from "@vuelidate/core";
import { required, minLength } from "@vuelidate/validators";
import { v4 as uuidv4 } from "uuid";
import Multiselect from "@vueform/multiselect";
import "@vueform/multiselect/themes/default.css";

const mobile_item_selec: any = null;
const mobile_last_move: any = null;
const nodesStatuses: any = {};
const fieldsContext: any = [];

export default defineComponent({
  name: "DrawFlowComponent",
  props: {
    authToken: String,
    blocks: Array,
    editorId: String,
    bot: Object,
    usesTemplate: Boolean,
    height: Number,
    showZoom: Boolean,
  },
  components: {
    EditorBlock,
    Multiselect,
  },
  validations() {
    return {
      botName: {
        required,
        minLength: minLength(5),
      },
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
      // eslint-disable-next-line @typescript-eslint/no-var-requires
      appVersion: require("../../../lib/version").version,
    };
  },
  data() {
    return {
      editor: null as any,
      mobile_item_selec,
      mobile_last_move,
      transform: "",
      isLoading: false,
      fieldsContext,
      nodesStatuses,
      validationStatus: false,
      botId: null,
      botName: "",
      botFrequency: 10,
      activation: {
        show: false,
        loading: false,
      },
      botFrequencyUnit: "minutes",
      scheduleOptions: ["minutes", "days"],
      calcBotFrequency: 10,
    };
  },
  async mounted() {
    try {
      if (this.editor) this.editor.clear();

      // Editor
      this.initDiagram();
      this.initContext();

      if (this.bot) {
        const obj = this.bot;
        const botData = obj?.json.data;
        this.botName = obj?.name;
        this.botFrequencyUnit = obj?.json.ux ? obj?.json.ux.frequencyUnit : "minutes";
        this.botFrequency = this.getDisplayFrequency(obj?.frequency, this.botFrequencyUnit);
        this.updateEditorBot(
          botData,
          obj?.json.context.nodes,
          this.botName,
          this.botFrequency,
          this.botFrequencyUnit
        );
      }
      const elements = document.getElementsByClassName("drag-drawflow");
      for (let i = 0; i < elements.length; i++) {
        elements[i].addEventListener("touchend", this.drop, false);
        elements[i].addEventListener("touchmove", this.positionMobile, false);
        elements[i].addEventListener("touchstart", this.drag, false);
      }
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const vm = this;
      EventEmitter.emitter.on("block-properties-change", function (block: string, data: any) {
        const json = vm.editor.drawflow.drawflow["Home"];
        if (json.data[block]) {
          json.data[block].data[data.id] = data.value;
        }
        vm.validationStatusCheck();
      });

      EventEmitter.emitter.on("editor-block-data-request", function (uuid: string) {
        const json = vm.editor.drawflow.drawflow["Home"];
        const node = json.context.nodes[uuid];
        if (node && json.data[node.id]) {
          const id = node.id;
          json.data[id]["parameters"] = node.parameters;
          json.data[id]["nodeName"] = node.name;
          json.data[id]["output_vars"] = node.output_vars;
          EventEmitter.emitter.emit(`editor-block-data-response-${uuid}`, json.data[id]);
        }
      });
      EventEmitter.emitter.on("editor-block-data-ctx-add", function (id: number, vars: []) {
        if (id) {
          for (const key in vars) {
            const item: any = vars[key];
            const value = `#${id}.${item.slug}`;
            if (!vm.fieldsContext.find((o: { value: string }) => o.value === value)) {
              vm.fieldsContext.push({ value, text: item.name, title: item.description });
            }
          }
        }
      });

      // Editor events
      this.editor.on("connectionCreated", this.sendContext);
      this.editor.on("connectionRemoved", this.sendContext);
      this.editor.on("nodeRemoved", this.removeNode);

      EventEmitter.emitter.on(
        "block-properties-error",
        function (blockId: number, onError: boolean) {
          vm.nodesStatuses[blockId] = onError;
          vm.validationStatusCheck();
        }
      );
    } catch (e) {
      console.error(e);
    }
  },
  watch: {
    bot: {
      immediate: true,
      deep: true,
      handler(bot, oldBot) {
        if ((oldBot && bot && bot.id !== oldBot.id) || !oldBot) {
          const obj = bot;
          const botData = obj?.json.data;
          if (botData)
            this.updateEditorBot(
              botData,
              obj?.json.context.nodes,
              obj?.name,
              obj?.frequency,
              obj?.json.ux ? obj?.json.ux.frequencyUntit : "minutes"
            );
        }
      },
    },
  },
  methods: {
    updateEditorBot(
      botData: any,
      botNodes: any,
      botName: string,
      botFrequency: number,
      botFrequencyUnit: string
    ) {
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const vm = this;
      this.botName = botName;
      this.botFrequency = botFrequency;
      this.botFrequencyUnit = botFrequencyUnit ? botFrequencyUnit : "minutes";
      setTimeout(() => {
        this.registerBlocks(botData);
        this.editor.import({
          drawflow: {
            Home: {
              context: {
                nodes: botNodes,
              },
              data: botData,
            },
          },
        });
        setTimeout(function () {
          vm.sendBotContext(botData);
          EventEmitter.emitter.emit("editor-valid-status", true);
        }, 160);
      }, 10);
    },
    export() {
      const exportdata = this.editor.export();
      const exportJson = exportdata.drawflow.Home;
      exportJson["ux"] = {
        frequencyUnit: this.botFrequencyUnit,
      };
      const jsonData = {
        name: this.botName,
        json: exportJson,
        frequency: this.calcBotFrequency,
      };
      return jsonData;
    },
    registerBlocks(blocks: any) {
      // Register Blocks
      for (const [, value] of Object.entries(blocks)) {
        this.registerBlock(value, (value as any).html);
      }
    },
    calculateRealFrequency() {
      if (this.botFrequencyUnit === "days") {
        this.calcBotFrequency = this.botFrequency * 24 * 60;
      } else if (this.botFrequencyUnit === "minutes") {
        this.calcBotFrequency = this.botFrequency;
      }
    },
    getDisplayFrequency(frequency: number, unit: string) {
      let result = frequency;
      if (unit === "days") {
        result = frequency / 24 / 60;
      }
      return result;
    },
    frequencyUnitChange(val: string) {
      this.botFrequencyUnit = val;
      this.calculateRealFrequency();
    },
    registerBlock(block: any, uid: string) {
      block["nuid"] = uid;
      const props = block;
      const options = {
        uid,
        parameters: block.parameters,
      };
      this.editor.registerNode(uid, EditorBlock, props, options);
    },
    onFrequencyChange(val: number) {
      this.botFrequency = val;
      this.calculateRealFrequency();
    },
    positionMobile(ev: any) {
      this.mobile_last_move = ev;
    },
    allowDrop(ev: any) {
      ev.preventDefault();
    },
    drag(ev: any) {
      if (ev.type === "touchstart") {
        this.mobile_item_selec = ev.target.closest(".drag-drawflow").getAttribute("data-node");
      } else {
        ev.dataTransfer.setData("node", ev.target.getAttribute("data-node"));
      }
    },
    drop(ev: any) {
      if (ev.type === "touchend") {
        const x = this.mobile_last_move!.touches[0].clientX;
        const y = this.mobile_last_move!.touches[0].clientY;
        const elFromPoint = document.elementFromPoint(x, y);
        const parentdrawflow = elFromPoint!.closest("#" + this.$props.editorId);
        if (parentdrawflow != null) {
          this.addNodeToDrawFlow(this.mobile_item_selec, JSON.parse(this.mobile_item_selec), x, y);
        }
        this.mobile_item_selec = "";
      } else {
        ev.preventDefault();
        const id = ev.dataTransfer.getData("node");
        const data = ev.dataTransfer.getData("data");
        this.addNodeToDrawFlow(id, JSON.parse(data), ev.clientX, ev.clientY);
      }
    },
    clearBot() {
      this.editor.clear();
      this.botName = "";
      this.initContext();
    },
    validationStatusCheck() {
      if (this.v$.$invalid) {
        EventEmitter.emitter.emit("editor-valid-status", false);
        return;
      }
      // Check nodes
      for (const [, value] of Object.entries(this.nodesStatuses)) {
        if (value) {
          this.validationStatus = false;
          EventEmitter.emitter.emit("editor-valid-status", false);
          return;
        }
      }
      // All nodes ok
      this.validationStatus = true;
      EventEmitter.emitter.emit("editor-valid-status", true);
    },
    getPreviousNodes(node: number, nodes?: []) {
      const data = this.editor.drawflow.drawflow["Home"].data;
      const inputs = data[node].inputs;
      let result: any = [];
      if (nodes) result = nodes;
      for (const key in inputs) {
        const input = inputs[key];
        for (let x = 0; x < input.connections.length; x++) {
          const connections = input.connections;
          result.push(connections[x].node);
          this.getPreviousNodes(connections[x].node, result);
        }
      }
      return Array.from(new Set(result));
    },
    getForwardNodesToBeUpdated(node: number, nodes?: []) {
      const data = this.editor.drawflow.drawflow["Home"].data;
      const outputs = data[node].outputs;
      let result: any = [];
      if (nodes) result = nodes;
      for (const key in outputs) {
        const output = outputs[key];
        for (let x = 0; x < output.connections.length; x++) {
          const connections = output.connections;
          if (connections[x].node !== node) {
            result.push(connections[x].node);
            this.getForwardNodesToBeUpdated(connections[x].node, result);
          }
        }
      }
      return Array.from(new Set(result));
    },
    initContext() {
      this.editor.drawflow.drawflow["Home"].context = {};
      this.editor.drawflow.drawflow["Home"].context.nodes = {};
      this.editor.drawflow.drawflow["Home"].context.appVersion = this.appVersion;
    },
    initDiagram() {
      // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
      const id = document.getElementById(this.$props.editorId!);
      this.editor = null;
      // @ts-expect-error test
      this.editor = new Drawflow(id, Vue);
      this.editor?.start();
    },
    addNodeToDrawFlow(id: string, data: any, pos_x: number, pos_y: number) {
      if (this.editor.editor_mode === "fixed") {
        return false;
      }
      pos_x =
        pos_x *
          (this.editor.precanvas.clientWidth /
            (this.editor.precanvas.clientWidth * this.editor.zoom)) -
        this.editor.precanvas.getBoundingClientRect().x *
          (this.editor.precanvas.clientWidth /
            (this.editor.precanvas.clientWidth * this.editor.zoom));
      pos_y =
        pos_y *
          (this.editor.precanvas.clientHeight /
            (this.editor.precanvas.clientHeight * this.editor.zoom)) -
        this.editor.precanvas.getBoundingClientRect().y *
          (this.editor.precanvas.clientHeight /
            (this.editor.precanvas.clientHeight * this.editor.zoom));
      const componentId = data.id;
      // Register node before drawing it
      const uid = uuidv4();
      this.registerBlock(data, uid);
      const node = this.editor.addNode(
        componentId, // name
        data.inputs, // inputs
        data.outputs, // outputs
        pos_x, // posx
        pos_y, // posy
        "class", // class
        {}, // data
        uid, // html
        "vue" // typenode
      );
      this.editor.drawflow.drawflow["Home"].context.nodes[uid] = {
        type: componentId,
        id: node,
        parameters: data.parameters,
        name: data.name,
        output_vars: data.output_vars,
      };
    },
    sendBotContext(data: any) {
      // For each node in data
      for (const key in data) {
        const destination = data[key].id;
        // const forwardNodes: any = this.getForwardNodesToBeUpdated(destination);
        const prevNodes: any = this.getPreviousNodes(parseInt(destination));
        // emit bot context change in destination
        this.updateBlockContext(destination, prevNodes);
      }
    },
    updateBlockContext(node: string, prevNodes: any) {
      EventEmitter.emitter.emit(
        `editor-block-bot-context-update-${node}`,
        this.fieldsContext,
        prevNodes
      );
    },
    removeNode(key: string) {
      delete this.nodesStatuses[key];
      if (Object.keys(this.nodesStatuses).length === 0) this.validationStatus = true;
    },
    sendContext(output: any) {
      const destination = output.input_id;
      const forwardNodes: any = this.getForwardNodesToBeUpdated(destination);
      const prevNodes: any = this.getPreviousNodes(parseInt(destination));
      EventEmitter.emitter.emit(
        `editor-block-bot-context-update-${destination}`,
        this.fieldsContext,
        prevNodes
      );
      for (let x = 0; x < forwardNodes.length; x++) {
        const prevNodes: any = this.getPreviousNodes(parseInt(forwardNodes[x]));
        EventEmitter.emitter.emit(
          `editor-block-bot-context-update-${forwardNodes[x]}`,
          this.fieldsContext,
          prevNodes
        );
      }
    },
  },
});
