import { defineComponent } from "vue";
import BlockProperties from "@/components/BlockProperties/index.vue";
import EventEmitter from "@/util/EventEmitter";

export default defineComponent({
  name: "EditorBlockComponent",
  props: {
    id: Number,
    description: String,
    logo_class: String,
    nodeData: Object,
    nuid: String,
  },
  components: {
    BlockProperties,
  },
  data() {
    return {
      showProperties: true,
      context: {} as any,
      nodeName: "",
      output_vars: [],
      parameters: [],
      botContext: {} as any,
      blockOnError: false,
      uid: this.$props.nodeData ? this.$props.nodeData.id : null,
    };
  },
  mounted() {
    const id = this.id;
    const nuid = this.nuid;
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    setTimeout(function () {
      EventEmitter.emitter.emit("editor-block-data-request", nuid, id);
    }, 40);
    EventEmitter.emitter.on(`editor-block-data-response-${nuid}`, this.setCtx);
    EventEmitter.emitter.on("block-properties-error", this.setError);
  },
  watch: {
    uid: {
      immediate: true,
      handler(val) {
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        EventEmitter.emitter.emit("editor-block-data-ctx-add", this.uid, this.output_vars);
        EventEmitter.emitter.on(`editor-block-bot-context-update-${val}`, this.setBotContext);
      },
    },
  },
  methods: {
    filterCtx(context: any, nodes: any) {
      const result: any = [];
      for (let x = 0; x < context.length; x++) {
        if (nodes.indexOf(context[x].value.substring(1, 2)) > -1) {
          result.push(context[x]);
        }
      }
      return result;
    },
    setBotContext(context: any, nodes: any) {
      this.botContext = this.filterCtx(context, nodes);
    },
    setCtx(ctx: any) {
      if (ctx) {
        this.context = {};
        for (const key in ctx.data) {
          this.context[key] = ctx.data[key];
        }
        this.nodeName = ctx.nodeName;
        this.uid = ctx.id;
        this.parameters = ctx.parameters;
        this.output_vars = ctx.output_vars;
      }
    },
    setError(blockId: string, error: boolean) {
      if (blockId === this.uid) this.blockOnError = error;
    },
    toggleSettings(st: boolean) {
      if (st) {
        this.showProperties = st;
      } else {
        this.showProperties = !this.showProperties;
      }
    },
  },
});
